import {addMethod, addMethodBatch, numberMethods, dateMethods, textMethods, financialMethods} from "@xbs/muon";
import group from "../operations/undo_group";

export function addMathMethods(){
	addMethodBatch(dateMethods);
	addMethodBatch(financialMethods);
	addMethodBatch(numberMethods);
	addMethodBatch(textMethods);
}

export function init(view){
	const math = {
		IMAGE: url => `<img class="webix_ssheet_cimage" src="${webix.template.escape(url)}">`,
		HYPERLINK: (url, text) => {
			const escape = webix.template.escape;
			let attrs;

			if(url[0] == "#")
				attrs = `href="javascript:void(0);" class="webix_ssheet_ref" data-ref="${escape(url.substr(1))}"`;
			else
				attrs = `target="blank" href="${escape(url)}"`;

			return `<a ${attrs}>${escape(text || url)}</a>`;
		},
		SPARKLINE: (arr, type, color1, color2) => {
			const config = { type, color: color1, negativeColor:color2, paddingX:2, paddingY:2 };

			if(type == "donut")
				webix.extend(config, {type:"pie", donut:1}, true);
			else if(type == "barH")
				webix.extend(config, {type:"bar", horizontal:1}, true);

			for(let i = 0 ; i < arr.length; i++)
				arr[i] = arr[i]||0;

			const tableConfig = view.$$("cells").config;

			//[FIXME] we do not know from which cell the method was called, so this is not the real width and height
			const isCircle = config.type == "pie" || type == "radar";
			const width = isCircle ? 100 : tableConfig.columnWidth;
			const height = isCircle ? 100 : tableConfig.rowHeight;
			return webix.Sparklines.getTemplate(config)(arr, { width, height });
		},
		CHECKBOX: state => !!state,
		RADIO: (rowPos, colPos, rowCount, colCount, state) => !!state
	};

	const keys = Object.keys(math);
	keys.forEach((key, i) => addMath(view, key, math[key], i == keys.length - 1));

	const grid = view.$$("cells");
	grid.on_click["ssheet_checkbox"] = function(ev, cell){
		const value = view.getCellValue(cell.row, cell.column, false);
		view.setCellValue(cell.row, cell.column, `=checkbox(${value ? 0 : 1})`);
		view.refresh();
	};
	grid.on_click["ssheet_radio"] = function(ev, cell){
		setRadioState(view, cell.row, cell.column);
	};

	view.attachEvent("onCommand", action =>{
		if(action.id == "add-radio")
			view.addRadio();
		else if(action.id == "add-checkbox")
			view.addCheckbox();
	});
}

export function setRadioState(view, row, column, state, page){
	const math = view.getCellValue(row, column, true, page);
	if(/^=(radio)\(/i.test(math)){
		const [rowPos, colPos, rowCount, colCount] = math.match(/\((.+?)\)/)[1].split(",").map(v => v.trim());

		if(state === false){
			view.setCellValue(row, column, `=radio(${rowPos}, ${colPos}, ${rowCount}, ${colPos}, 0)`, page);
			view.refresh();
		}
		else{
			const value = view.getCellValue(row, column, false);
			if(!value)
				group.set(()=>{
					for(let r = 1; r <= rowCount; r++)
						for(let c = 1; c <= colCount; c++)
							view.setCellValue(
								row - rowPos + r,
								column - colPos + c,
								`=radio(${r}, ${c}, ${rowCount}, ${colCount}, ${r == rowPos && c == colPos ? 1 : 0})`,
								page
							);
					view.refresh();
				});
		}
	}
}

export function addMath(view, name, handler, sort){
	name = name.toUpperCase();
	addMethod(name, handler);
	if(view.$$("liveEditor")){
		[
			webix.$$(view.$$("liveEditor").config.suggest).getList(),
			webix.$$(view.$$("formulaButton").config.popup).getBody()
		].forEach(list => {
			if(!list.exists(name)){
				list.add({id:name, value:name});
				if(sort)
					list.sort("value", "asc", "string");
			}
		});
	}
}

export function getJsDateFromExcel(excelDate) {
	const date = new Date(Math.round((excelDate - 25569) * 86400 * 1000));
	return new Date(date.getTime() + date.getTimezoneOffset() * 60000);
}

export function getExcelDateFromJs(jsDate) {
	const returnDateTime = 25569 + ((jsDate.getTime() - (jsDate.getTimezoneOffset() * 60000)) / (86400 * 1000));
	return returnDateTime.toString();
}

export function getMathTemplate(math, value){
	if(typeof math == "string"){
		if(/^=(checkbox)\(/i.test(math))
			return `<span class='webix_icon ssheet_checkbox wxi-checkbox-${value ? "marked" : "blank"}'></span>`;
		else if(/^=(radio)\(/i.test(math))
			return `<span class='webix_icon ssheet_radio wxi-radiobox-${value ? "marked" : "blank"}'></span>`;
	}
	return value;
}